<script>
import $ from 'jquery'
import {
    ajaxGetProducts,ajaxPostProducts
} from '@/api/products' 

import {ajaxImageUpload} from '@/api/images'
export default {
    data: () => ({
        products: [],
        tempProduct:{
                category: "",
                content: "",
                description: "",
                image: "",
                imageUrl:'',
                is_enabled: 0,
                num: 0,
                origin_price: 0,
                price: 0,
                title: "",
                unit: ""
        },
        isLoading:false,
        status: {
          fileUploading:false, 
        }
    }),
    methods: {
        getProducts() {
            this.isLoading = true
            ajaxGetProducts().then(res => {
                this.products = res.data.products
                this.isLoading = false
            })
        },
        openModal() {
            $('#productModal').modal('show')
        },
        updateProduct(){
             
            ajaxPostProducts({data:this.tempProduct}).then(res=>{
                // console.log('product',res.data);
            }) 
        },
        uploadFile(){
            
            const uploadFile = this.$refs.files.files[0]
            const formData = new FormData()
            formData.append('file-to-upload',uploadFile)
             this.status.fileUploading = true
            ajaxImageUpload(formData,{
                    headers:{
                        'Content-Type':'multipart/form-data',
                        'Access-Control-Allow-Origin': '*'
                    },
                    
                }).then(()=>{
                     this.status.fileUploading = false
                })
        } 
    },
    created() {
        this.getProducts()
        this.$bus.$emit('message:push','ä¸æ®µè¨æ¯','success')
    },
}
</script>

